import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "bootstrap/dist/css/bootstrap.min.css";
import "./CSS/footer.css";
import footimg from "./image/footer.png";
//-------material icons-------//
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import YouTubeIcon from "@mui/icons-material/YouTube";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import PhoneIcon from "@mui/icons-material/Phone";
import LocalPostOfficeIcon from "@mui/icons-material/LocalPostOffice";

function Footer() {
  return (
    <>
      <Container fluid>
        <Row>
          <Col sm={12} className="footer-bg">
            <Row>
              <Col sm={3}>
                <img src={footimg} alt="footerimg" className="footrtimg" />
                <div className="icon-group">
                  <FacebookIcon style={{ fontSize: 45, color: "white" }} />
                  <InstagramIcon style={{ fontSize: 45, color: "white" }} />
                  <YouTubeIcon style={{ fontSize: 45, color: "white" }} />
                  <LinkedInIcon style={{ fontSize: 45, color: "white" }} />
                </div>
              </Col>
              <Col sm={2} className="footer-links">
                <h5 className="link-head">Quick Links</h5>
                <div className="ourlinks">
                  <p>Home</p>
                  <p>About Us</p>
                  <p>Blog</p>
                  <p>Gallary</p>
                </div>
              </Col>
              <Col sm={2} className="footer-links">
                <h5 className="link-head">Our Service</h5>
                <div className="ourlinks">
                  <p> Doctor Consultation</p>
                  <p>Free Health Care</p>
                  <p>Doctors List</p>
                  <p>Physicians</p>
                </div>
              </Col>
              <Col sm={2} className="footer-links">
                <h5 className="link-head">Our Product</h5>
                <div className="ourlinks">
                  <p>Health Devices</p>
                  <p>Personal Care</p>
                  <p>Dolo 650</p>
                  <p>Covid Essentials</p>
                </div>
              </Col>
              <Col sm={3} className="footer-links">
                <h5 className="link-head">Reach Us at</h5>
                <div className=" ">
                  <p className="">
                    <PhoneIcon />
                    <span>&nbsp;</span>
                    +91- 6382981082
                  </p>
                  <p className="">
                    <LocalPostOfficeIcon />
                    <span>&nbsp;</span>
                    reachroyal@gmail.com
                  </p>
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row>
          <Col sm="12" className="copyright-bg">
            <div className="">
              <p className="">Copyright@Lokesh | All Rights Reserved</p>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
}
export default Footer;
