import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "bootstrap/dist/css/bootstrap.min.css";
/*---------------CSS---------------*/
import "./CSS/Contact.css";
import Button from "react-bootstrap/Button";

function Contact() {
  return (
    <>
      <Container fluid className="form-bg">
        <Row>
          <Col sm="12">
            <p className="Contact-Head">Looking For Consultation With Us ?</p>
          </Col>
        </Row>
        <Row>
          <Col sm="12">
            <form for html>
              <Row>
                <Col sm="6" className="Contact-form1">
                  <input
                    type="text"
                    label="number"
                    placeholder="Enter your Name"
                    className="form-input1"
                  />
                  <input
                    type="Email"
                    label="number"
                    placeholder="Enter your Email"
                    className="form-input2"
                  />
                  <br />
                  <textarea
                    type="text"
                    rows="6"
                    label="number"
                    cols="50"
                    placeholder="Enter your Message"
                    className="form-input3"
                  />
                </Col>
              </Row>
            </form>
            <Button type="submit" className="submitmessage">
              {" "}
              Send Message
            </Button>
          </Col>
        </Row>
        <Row>
          <Col sm={12} style={{ paddingLeft: "0px", paddingRight: "0px" }}>
            <iframe
              title="location"
              src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d15562.411500674498!2d78.73628385!3d12.8042698!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sin!4v1704347402090!5m2!1sen!2sin"
              style={{
                width: "-webkit-fill-available",
                height: "400px",
                style: "border:0;",
              }}
              allowfullscreen=""
              loading="lazy"
              referrerpolicy="no-referrer-when-downgrade"
            ></iframe>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Contact;
