import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "bootstrap/dist/css/bootstrap.min.css";
import "./CSS/aboutus.css";

function Aboutus() {
  return (
    <>
      <Container>
        <Row>
          <Col sm={12}>
            <h6 className="About-head">About us</h6>
          </Col>
          <Col sm={12}>
            <p className="abtpara-1">
              Medical HospitalIndia's 1st multi-speciality hospital for senior
              citizens, is the brainchild of renowned Geriatricians, Dr. LPR and
              Dr. Srinivas. Their extensive experience has enabled them to bring
              together a team of professionals from various medical disciplines
              who possess a deep understanding of the complexities and common
              health conditions associated with the elderly.
            </p>
            <p className="abtpara-2">
              The revolutionary approach at Geri Care involves a cohesive and
              coordinated medical intervention that redefines elderly care and
              collaboratively designs personalised and comprehensive care plans,
              eliminating the need for multiple consultations with minimal or no
              coordination.
            </p>
          </Col>
        </Row>
      </Container>
    </>
  );
}
export default Aboutus;
